* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
#logo {
    position: relative;
    top: -45px;
    left: -105px;
    width: 300px;
    height: 170px;
    z-index: 1;
}

@media(max-width: 850px) {
    #logo {
        position: relative;
        top: -20px;
        left: -70px;
        width: 190px;
        height: 100px;
        z-index: 1;
    }
}
#navbar {
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    top: 0;
    width: 96vw;
    height: 11vh;
    background-color: #fff;
    z-index: 0;
    font-family: 'Montserrat', sans-serif;
    box-shadow: 0px 1px 0.4px rgb(179, 170, 170, 0.6);
}

#navbar ul {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    list-style: none;
    margin-left: 4rem;
}

#navbar ul li {
    padding: 1.5rem;
    color: black;
}

@media(max-width:850px) {
    #navbar {
        width: 200px;
        height: 7vh;
        box-shadow: none;
    }
    #navbar ul {
        display: flex;
        flex-direction: row;
        list-style: none;
        justify-content: space-between;
        list-style: none;
        margin-left: 25rem;
    }
    #navbar ul li {
        padding: 0.9rem;
    }
}
.showcase {
